.wrap {
    position: relative;
    width: 100%;
    height: 100%;
}

.slide {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    padding: 11rem 15.44rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    user-select: none;
    &_mobile {
        width: 100%;
    }
    &.active {
        opacity: 1;
        z-index: 100;
        .clock {
            .clock__second {
                animation: second 60s steps(60, end) 4.5s infinite;
            }
            .clock__minute {
                animation: minute 4s ease-in-out 0.5s 1;
            }
            .clock__hour {
                animation: hour 4s ease-in-out 0.5s 1;
            }
        }
    }
    &_0 {
        .container {
            max-height: 63.9rem;
            padding-top: 5.1rem;
        }
        .h1 {
            width: 59.69rem;
        }
        .text {
            margin-left: 5rem;
            width: 66rem;
        }
        .btn {
            position: absolute;
            margin: 0;
            left: 50%;
            bottom: 0;
            transform: translate(-50%);
        }
    }
    &_1 {
        .container {
            padding-top: 0.5rem;
            max-height: 54.4rem;
        }
        .h3 {
            margin-top: auto;
        }
        .text {
            width: 61rem;
        }
    }
    &_2 {
        .container {
            height: auto;
        }
        .h2 {
            margin-left: 78rem;
            width: 54.6rem;
        }
    }
    &_3 {
        .container {
            height: auto;
            padding-bottom: 7rem;
        }
        .h2 {
            width: 54.6rem;
        }
    }
    &_4 {
        .container {
            max-height: 53.3rem;
        }
        .h2 {
            margin-left: 77.5rem;
            width: 54.6rem;
        }
    }
    &_5 {
        .container {
            height: auto;
        }
        .h2 {
            width: 54.6rem;
        }
    }
    &_6 {
        .container {
            max-height: 54.7rem;
            flex-direction: column;
        }
        .h3 {
            margin-left: auto;
            width: 64.6rem;
        }
        .text {
            margin-top: 6rem;
            margin-left: auto;
            width: 51.6rem;
        }
    }
    &_7 {
        .container {
            max-height: 64.8rem;
            padding-bottom: 5rem;
        }
        .h3 {
            margin-top: auto;
            width: 38.6rem;
        }
        .text {
            width: 63.8rem;
        }
    }
    &_8 {
        .container {
            height: auto;
        }
        .h2 {
            width: 54.6rem;
        }
    }
    &_9 {
        .container {
            max-height: 53.7rem;
        }
        .h2 {
            margin-left: auto;
            width: 51.6rem;
        }
    }
    &_10 {
        .container {
            height: auto;
        }
        .h2 {
            width: 54.6rem;
        }
    }
    &_11 {
        .container {
            max-height: 53.7rem;
            flex-direction: column;
        }
        .h3 {
            width: 54.6rem;
        }
        .desc {
            margin-top: 6.5rem;
            margin-left: 14.9rem;
            width: 38rem;
        }
    }
    &_12 {
        .container {
            max-height: 64.7rem;
        }
        .h3 {
            margin-top: auto;
            width: 60.6rem;
        }
    }
    &_13 {
        .container {
            max-height: 64.8rem;
            padding-bottom: 5rem;
        }
        .h3 {
            margin-top: auto;
