        }
    }
    &_14 {
        .container {
            height: auto;
            padding-top: 12rem;
        }
        .h2 {
            width: 54.6rem;
        }
    }
    &_15 {
        .container {
            height: auto;
            padding-top: 6rem;
        }
        .h2 {
            width: 54.6rem;
        }
    }
}

.overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 200vw;
    width: 200vw;
    transform: translate(-50%, -50%) scale(0);
    background: var(--light-grey);
    border-radius: 50%;
    z-index: -1;
    transition: all ease-in-out 0.5s;
}

.again {
    margin-top: 5.1rem;
    display: flex;
    align-items: center;
    transition: all ease-in-out 0.5s;
    opacity: 0;
    font-size: 2.50rem;
    line-height: 3.63rem;
    color: var(--bright-green);
    &.shown {
        opacity: 1;
        pointer-events: auto;
        cursor: pointer;
    }
    svg {
        margin-right: 1.63rem;
        width: 4.56rem;
    }
}

.col {
    width: 50%;
    display: flex;
    height: 100%;
    flex-direction: column;
}

.ul {
    font-size: 2.50rem;
    line-height: 3.63rem;
    list-style: none;
    li+li {
        margin-top: 2.5rem;
    }
    li {
        display: flex;
    }
}

.li__icon {
    margin-right: 2.81rem;
    width: 3.50rem;
    height: 3.50rem;
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: baseline;
}

.container {
    width: 100%;
    max-height: 55rem;
    display: flex;
    height: 100%;
}

.desc {
    position: relative;
    font-size: 2.25rem;
    line-height: 3.63rem;
    font-weight: 500;
    &::before {
        position: absolute;
        content: '*';
        font: inherit;
        top: 0rem;
        left: -2rem;
        color: var(--emerald-green);
    }
}

.btn {
    margin-top: 15.63rem;
    margin-left: 10rem;
    width: 8.75rem;
    height: 8.75rem;
    border-radius: 62.50rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bright-green);
    animation: btn 3s ease-in-out infinite;
    transition: all ease-in-out 0.5s;
    opacity: 0;
    &.shown {
        opacity: 1;
    }
    img {
        margin-right: 0.28rem;
        width: 4.06rem;
        height: 4.94rem;
    }
    &__text {
        width: 0;
        transition: width ease-in-out 0.5s;
        overflow: hidden;
    }
    &__inner {
        padding-left: 2.63rem;
        white-space: nowrap;
        font-size: 1.50rem;
        color: var(--white);
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.32em;
    }
    // &:hover {
    //     width: 51.63rem;
    //     .btn__text {
    //         width: 39.59rem;
    //     }
    // }
}

.container {
    position: relative;
}

.play {
    position: absolute;
    width: 15.63rem;
    height: 15.63rem;
    left: 50%;
    top: 39rem;
    transform: translate(-50%);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bright-green);
    transition: all ease-in-out 0.5s;
    opacity: 0;
    pointer-events: none;
    z-index: 110;
    cursor: pointer;
    &.shown {
        opacity: 1;
        pointer-events: auto;
    }
    svg {
        margin-left: 0.7rem;
        width: 5.63rem;
    }
}

.h1, .h2, .h3, .h4, .desc, .text, .ul {
    &::before{
        opacity: 0;
        transition-property: opacity;
        transition-duration: 0.4s;
    }
    span, li {
        opacity: 0;
        filter: blur(0.25rem);
        transition-property: opacity, filter;
        transition-duration: 0.4s, 0.4s;
    }
}

.h1.shown, .h2.shown, .h3.shown, .h4.shown, .desc.shown, .text.shown, .ul.shown {
    &::before{
        
        opacity: 1;
    }
    span, li {
        opacity: 1;
        filter: blur(0);
        &:nth-child(1) {
            transition-delay: 0s, 0s;
        }
        &:nth-child(2) {
            transition-delay: 0.05s, 0.05s;
        }
        &:nth-child(3) {
            transition-delay: 0.1s, 0.1s;
        }
        &:nth-child(4) {
            transition-delay: 0.15s, 0.15s;
        }
        &:nth-child(5) {
            transition-delay: 0.2s, 0.2s;
        }
        &:nth-child(6) {
            transition-delay: 0.25s, 0.25s;
        }
        &:nth-child(7) {
            transition-delay: 0.3s, 0.3s;
        }
        &:nth-child(8) {
            transition-delay: 0.35s, 0.35s;
        }
        &:nth-child(9) {
            transition-delay: 0.4s, 0.4s;
        }
        &:nth-child(10) {
            transition-delay: 0.45s, 0.45s;
        }
        &:nth-child(11) {
            transition-delay: 0.5s, 0.5s;
        }
        &:nth-child(12) {
            transition-delay: 0.55s, 0.55s;
        }
        &:nth-child(13) {
            transition-delay: 0.6s, 0.6s;
        }
        &:nth-child(14) {
            transition-delay: 0.65s, 0.65s;
        }
        &:nth-child(15) {
            transition-delay: 0.7s, 0.7s;
        }
        &:nth-child(16) {
            transition-delay: 0.75s, 0.75s;
        }
        &:nth-child(17) {
            transition-delay: 0.8s, 0.8s;
        }
    }
}

.text {
    font-size: 2.50rem;
    line-height: 3.63rem;
    font-weight: 500;
}

.qr {
    display: flex;
    transition: all ease-in-out 0.5s;
    opacity: 0;
    &.shown {
        opacity: 1;
    }
    img {
        margin-right: 4rem;
        width: 13.56rem;
        height: 13.56rem;
    }
    .text {
        margin-top: -0.5rem;
        width: 62rem;
    }
}

@keyframes second {
    0% {
        transform: translate(-50%, 0%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, 0%) rotate(360deg);
    }
}

@keyframes minute {
    0% {
        transform: translate(-50%, 0%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, 0%) rotate(8640deg);
    }
}

@keyframes hour {
    0% {
        transform: translate(-50%, 0%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, 0%) rotate(720deg);
    }
}

@keyframes btn {
    0% {
        transform: translate(0rem, 0rem);
    }
    50% {
        transform: translate(-10rem, 0rem);
    }
    100% {
        transform: translate(0rem, 0rem);
    }
}

