.wrap {
    opacity: 0;
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: opacity ease 0.3s;
    &.hide {
        opacity: 0 !important;
    }
}

.overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 200vw;
    width: 200vw;
    transform: translate(-50%, -50%) scale(1);
    background: var(--light-grey);
    border-radius: 50%;
    z-index: 10;
}

.body, .points, .svgs, .svg, .container, .labels, .charts, .cities {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.charts {
    z-index: 9;
}

.chart {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 53.13rem;
    height: 53.13rem;
    &:nth-child(1) {
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-90deg);
        }
        span {
            position: absolute;
            left: 50%;
            top: 30.56rem;
            transform: translate(-50%);
            font-size: 10.00rem;
            line-height: 10.00rem;
            font-weight: 700;
            color: var(--white);
            font-family: 'Halvar Breit Rg';
            z-index: 1;
        }
    }
    &:nth-child(2) {
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    &__logo {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 2;
        span {
            margin-top: 2.31rem;
            font-size: 2.50rem;
            line-height: 2.50rem;
            font-weight: 700;
            color: var(--white);
            font-family: 'Halvar Breit Rg';
            z-index: 1;
            text-align: center;
        }
        &:nth-child(1) {
            left: 4rem;
            top: 16.8rem;
            img {
                width: 20.06rem;
            }
        }
        &:nth-child(2) {
            left: 33.9rem;
            top: 16.8rem;
            img {
                width: 10.94rem;
            }
            span {
                margin-top: 1.69rem;
            }
        }
        &:nth-child(3) {
            left: 20.2rem;
            top: 38rem;
            img {
                width: 12.69rem;
            }
        }
    }
}

.points, .cities {
    z-index: 10;
}

.point {
    position: absolute;
    width: 3.00rem;
    height: 3.00rem;
    left: 23.7%;
    top: 47.1%;
    span {
        position: absolute;
        left: 50%;
        top: 50%;
        width: 100%;
        height: 100%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
    }
}

.point span {
    &:nth-child(3) {
        background: var(--bright-green);
    }
    &:nth-child(2) {
        border: 0.25rem solid var(--bright-green);
        animation: pulse 3s linear infinite;
    }
    &:nth-child(1) {
        border: 0.25rem solid var(--bright-green);
        animation: pulse 3s linear 1.5s infinite;
    }
}

.city {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__info {
