.container {
    top: 50%;
    left: 50%;
    height: auto;
}

.bodies {}

.body {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: auto;
    &:nth-child(1) {
        transform: translate(0);
    }
    &:nth-child(3), &:nth-child(4) {
        &::after {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 4rem;
            background: linear-gradient(to bottom, var(--light-grey), transparent);
            opacity: 0;
            transition: opacity ease 0.3s;
            z-index: 1000;
        }
        &::before {
            position: absolute;
            content: '';
            bottom: 0;
            left: 0;
            width: 100%;
            height: 4rem;
            background: linear-gradient(to top, var(--light-grey), transparent);
            opacity: 0;
            transition: opacity ease 0.3s;
            z-index: 1000;
        }
    }
}

.bg {
    position: relative;
    width: 100%;
    height: 100%;
    &__tiles {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        opacity: 0.5;
        // img{
        //     // margin: -0.05rem;
        //     width: 20%;
        //     height: auto;
        // }
    }
    &_tile {
        display: flex;
        width: 20%;
        height: auto;
        margin: -0.1rem 0;
    }
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--light-grey);
        z-index: 10;
    }
}

.svgs {
    z-index: 11;
}

.svg {
    display: flex;
    height: auto;
    width: 100%;
    z-index: 100;
    &_1 {
        width: 140.44rem;
        z-index: 101;
        top: 40.5%;
        left: 6.1%;
    }
    &_2 {
        width: 116.13rem;
        left: 50%;
        transform: translate(45.9%, -0.8%);
    }
}

.labels {
    z-index: 102;
}

.placeholder {
    display: none;
}

.countries {
