@keyframes pulse {
    0% {
        transform: translate(-50%, -50%) scale(0);
        border-width: 0.25rem;
        opacity: 1;
    }
    40% {
        transform: translate(-50%, -50%) scale(2);
        opacity: 0.9;
        border-width: 0.09rem;
    }
    100% {
        transform: translate(-50%, -50%) scale(4);
        opacity: 0;
        border-width: 0;
    }
}

@media only screen and (max-width: 767px) and (max-aspect-ratio: 8/15) {
    .body:nth-child(3), .body:nth-child(4) {
        &::after, &::before {
            opacity: 1;
        }
    }
}

@media only screen and (max-width: 767px) {
    .wrap {
        &_popup {
            z-index: 1000;
        }
    }
    .btn {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        left: 50%;
        bottom: 3.00rem;
        width: 15.63rem;
        height: 2.50rem;
        border-radius: 6.25rem;
        z-index: 1000;
        background: var(--bright-green);
        color: var(--white);
        font-weight: bold;
        line-height: 1.25rem;
        pointer-events: auto;
        opacity: 0;
        svg {
            margin-right: 0.56rem;
            width: 1.25rem;
        }
        span {
            display: flex;
            padding-bottom: 0.16rem;
        }
        &.hide {
            display: none;
        }
    }
    .close {
        position: absolute;
        top: 0.63rem;
        right: 0.63rem;
        pointer-events: none;
        z-index: 1000;
        width: 3.31rem;
        height: 3.31rem;
        &.shown {
            display: flex;
            opacity: 1;
            pointer-events: auto;
        }
    }
    .overlay {
        height: 200vh;
        width: 200vh;
    }
    .label {
        padding: 0 0.38rem;
        height: 0.75rem;
        font-size: 0.44rem;
        line-height: 1.25rem;
    }
    .container {
        height: 100%;
        &.active {
            .placeholder {
                display: none;
            }
            .body:nth-child(1) {
                transform: translate(-24.7%, -50%) !important;
            }
            .btnInfo {
                opacity: 1;
            }
        }
    }
    .placeholder {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0%, -50%);
        width: 100vw;
        height: auto;
        display: flex;
        z-index: 100;
        picture {
            display: flex;
            width: 100%;
        }
    }
    .body {
        display: none;
        &:nth-child(1) {
            width: 80.00rem;
            transform: translate(0);
        }
        &:nth-child(2) {
            width: 100%;
            left: 0;
            top: 0;
            transform: translate(0);
        }
        &:nth-child(3) {
            width: 31rem;
            left: 0;
            top: 50%;
            transform: translate(0%, -50%);
            overflow: hidden;
            .cities {
                width: 67rem;
                left: -100%;
            }
            .bg {
                left: -100%;
                width: 67rem;
            }
        }
        &:nth-child(4) {
            width: 40rem;
            left: 0;
            top: 50%;
            transform: translate(0%, -50%);
            overflow: hidden;
            .cities {
                width: 67rem;
                left: -38%;
            }
            .bg {
                left: -38%;
                width: 67rem;
            }
        }
    }
    .svg {
        &_1 {
            width: 70.13rem;
            top: 40.2%;
            left: 6.1%;
        }
    }
    .point {
        width: 0.75rem;
        height: 0.75rem;
        left: 48.2%;
        top: 47.7%;
    }
    .point span {
        &:nth-child(1) {
            width: 4.00rem;
            height: 4.00rem;
            border: 0.13rem solid var(--bright-green);
        }
        &:nth-child(2) {
            width: 4.00rem;
            height: 4.00rem;
            border: 0.13rem solid var(--bright-green);
        }
    }
    .label {
        &_13 {
            font-size: 0.56rem;
            line-height: 0.56rem;
            height: 1.06rem;
            padding: 0 0.75rem;
            top: 54.6%;
            left: 23.3%;
        }
    }
    .btnInfo {
        position: absolute;
        left: 50%;
        top: 30vh;
        width: 4.00rem;
        height: 4.00rem;
        border-radius: 62.50rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--bright-green);
        animation: btn 3s ease-in-out infinite;
        opacity: 0;
        pointer-events: none;
        z-index: 1000;
        transition: opacity ease 0.2s;
        &.shown {
            opacity: 1;
        }
        img {
            margin-right: 0.13rem;
            width: 1.88rem;
            height: 2.25rem;
        }
    }
    .city {
        &__point {
            margin: 0.13rem 0;
            width: 1.00rem;
            height: 1.00rem;
        }
        &__name {
            font-size: 0.56rem;
            line-height: 1rem;
        }
        &__desc {
            margin-top: -0.25rem;
            margin-top: -0.25rem;
            font-size: 0.50rem;
            line-height: 1rem;
        }
    }
    @keyframes pulse {
        0% {
            transform: translate(-50%, -50%) scale(0);
            opacity: 1;
        }
        40% {
            transform: translate(-50%, -50%) scale(0.4);
            opacity: 1;
        }
        100% {
            transform: translate(-50%, -50%) scale(1);
            opacity: 0;
        }
    }
    @keyframes btn {
        0% {
            transform: translate(calc(-50% - 2rem), -50%);
        }
        50% {
            transform: translate(calc(-50% + 2rem), -50%);
        }
        100% {
            transform: translate(calc(-50% - 2rem), -50%);
        }
    }
}