.wrap {
    position: relative;
    width: 100%;
    height: 100%;
    sup {
        margin-left: 0.1em;
        font-size: 0.65em;
        line-height: 1.8em;
    }
}

.slide {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    padding: 11rem 15.44rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    user-select: none;
    &_mobile {
        width: 100%;
    }
    &.active {
        opacity: 1;
        z-index: 100;
    }
    &_0 {
        .container {
            padding-left: 51.6rem;
            max-height: 64rem;
            height: 100%;
            display: flex;
            flex-direction: column;
        }
        .h1 {
            margin-top: 5rem;
            width: 78.31rem;
            margin-bottom: 5.75rem;
        }
        .text {
            width: 77.38rem;
            &+.text {
                margin-top: 3.63rem;
            }
        }
        .btn {
            margin-top: auto;
        }
    }
    &_2, &_3, &_4 {
        .container {
            height: 100%;
            max-height: 59rem;
        }
    }
    &_2 {
        .text {
            width: 52rem;
        }
    }
    &_4 {
        .text+.text {
            width: 41.7rem;
        }
    }
    &_5 {
        .h2 {
            font-size: 7.5rem;
            line-height: 7.5rem;
        }
        .container {
            padding-top: 4.7rem;
        }
    }
    &_6 {
        .text {
            margin-left: auto;
            width: 51.63rem;
        }
    }
    &_7 {
        .link {
            margin-top: 3rem;
            margin-left: 12.50rem;
        }
        .container {
            display: flex;
            height: 100%;
            max-height: 55rem;
            flex-direction: column;
            align-items: flex-start;
        }
        .h2 {
            width: 59.63rem;
        }
        .text {
            margin-top: 8.63rem;
            margin-left: 12.50rem;
            width: 39.06rem;
        }
    }
}

.overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 200vw;
    width: 200vw;
    transform: translate(-50%, -50%) scale(0);
    background: var(--light-grey);
    border-radius: 50%;
    z-index: -1;
    transition: all ease-in-out 0.5s;
}

.col {
    width: 50%;
    display: flex;
    height: 100%;
    flex-direction: column;
}

.table {
    margin-top: 5.69rem;
    display: flex;
    flex-direction: column;
    width: 100%;
    &__row {
        margin-bottom: 2.5rem;
        display: flex;
        width: 100%;
        height: 7.50rem;
        align-items: center;
        font-size: 3.75rem;
        line-height: 3.75rem;
        font-family: 'Halvar Breit Rg';
        font-weight: 700;
        color: var(--emerald-green);
        opacity: 0;
        filter: blur(0.25rem);
        transition-property: opacity, filter;
        transition-duration: 0.4s, 0.4s;
        &:nth-child(1) {
            margin-bottom: 2.13rem;
            height: auto;
            font-size: 2.50rem;
            line-height: 3.63rem;
            font-family: 'Manrope';
            color: var(--darkest-green);
        }
        &:nth-child(2n) {
            position: relative;
            z-index: 1;
            &::before {
                position: absolute;
                content: '';
                top: 0;
                left: 50%;
                transform: translate(-50%);
                width: calc(100% + 5rem);
                height: 100%;
                background: var(--white);
                z-index: -1;
            }
        }
        &:last-child {
            margin-bottom: 0;
        }
        &.shown {
            opacity: 1;
            filter: blur(0);
        }
    }
    &__col {
        display: flex;
        width: calc((100% - 57.3rem)/3);
        &:nth-child(1) {
            width: 63rem;
        }
    }
    .hidden {
        display: none;
    }
    .red {
        color: var(--red);
    }
    .star {
        color: var(--bright-green);
        font-size: 2.50rem;
        line-height: 3.5rem;
        font-weight: 500;
    }
}

.description {
    position: relative;
    margin-top: 2.34rem;
    margin-left: 64.6rem;
    font-size: 2.5rem;
    line-height: 4.43rem;
    font-weight: 500;
    width: 65.44rem;
    color: var(--dark-grey);
    span {
        position: relative;
    }
    span::after {
        position: absolute;
        content: '*';
        left: -1.63rem;
        top: 0.6rem;
        width: 1.13rem;
        font: inherit;
        line-height: 3.63rem;
        color: var(--bright-green);
    }
}

.desc {
    font-size: 1.5rem;
    line-height: 2rem;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0.32em;
    color: var(--darkest-green);
}

.btn {
    margin-left: 10rem;
    width: 8.75rem;
    height: 8.75rem;
    border-radius: 62.50rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bright-green);
    animation: btn 3s ease-in-out infinite;
    transition: all ease-in-out 0.5s;
    opacity: 0;
    &.shown {
        opacity: 1;
    }
    img {
        margin-right: 0.28rem;
        width: 4.06rem;
        height: 4.94rem;
    }
    &__text {
        width: 0;
        transition: width ease-in-out 0.5s;
        overflow: hidden;
    }
    &__inner {
        padding-left: 2.63rem;
        white-space: nowrap;
        font-size: 1.50rem;
        color: var(--white);
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.32em;
    }
    // &:hover {
    //     width: 51.63rem;
    //     .btn__text {
    //         width: 39.59rem;
    //     }
    // }
}

.container {
    position: relative;
}

.link {
    opacity: 0;
    transition-property: opacity;
    transition-duration: 0.4s;
}

.h1, .h2, .h3, .h4, .desc, .text, .ul, .description, .link {
    span, li {
        opacity: 0;
        filter: blur(0.25rem);
        transition-property: opacity, filter;
        transition-duration: 0.4s, 0.4s;
    }
}

.link.shown {
    opacity: 1;
}

.h1.shown, .h2.shown, .h3.shown, .h4.shown, .desc.shown, .text.shown, .ul.shown, .description.shown, .link.shown {
    span, li {
        opacity: 1;
        filter: blur(0);
        &:nth-child(1) {
            transition-delay: 0s, 0s;
        }
        &:nth-child(2) {
            transition-delay: 0.05s, 0.05s;
        }
        &:nth-child(3) {
            transition-delay: 0.1s, 0.1s;
        }
        &:nth-child(4) {
            transition-delay: 0.15s, 0.15s;
        }
        &:nth-child(5) {
            transition-delay: 0.2s, 0.2s;
        }
        &:nth-child(6) {
            transition-delay: 0.25s, 0.25s;
        }
        &:nth-child(7) {
            transition-delay: 0.3s, 0.3s;
        }
        &:nth-child(8) {
            transition-delay: 0.35s, 0.35s;
        }
        &:nth-child(9) {
            transition-delay: 0.4s, 0.4s;
        }
        &:nth-child(10) {
            transition-delay: 0.45s, 0.45s;
        }
        &:nth-child(11) {
            transition-delay: 0.5s, 0.5s;
        }
        &:nth-child(12) {
            transition-delay: 0.55s, 0.55s;
        }
        &:nth-child(13) {
            transition-delay: 0.6s, 0.6s;
        }
        &:nth-child(14) {
            transition-delay: 0.65s, 0.65s;
        }
        &:nth-child(15) {
            transition-delay: 0.7s, 0.7s;
        }
        &:nth-child(16) {
            transition-delay: 0.75s, 0.75s;
        }
        &:nth-child(17) {
            transition-delay: 0.8s, 0.8s;
        }
    }
}

.card {
    margin-left: -3.4rem;
    padding: 2.88rem 2rem 2.88rem 3.38rem;
    display: flex;
    flex-direction: column;
    width: 60.63rem;
    border-radius: 1.88rem;
    background: rgba(255, 255, 255, 0.95);
    opacity: 0;
    transition-property: opacity;
    transition-duration: 0.4s;
    .h4 {
        margin-top: 2.50rem;
    }
    .text {
        margin-top: 2.5rem;
        font-size: 2rem;
        line-height: 3.00rem;
        &+.text {
            margin-top: 3rem;
        }
    }
    &.shown {
        opacity: 1;
    }
}

.text {
    font-size: 2.50rem;
    line-height: 3.63rem;
    font-weight: 500;
}

@keyframes btn {
    0% {
        transform: translate(0rem, 0rem);
    }
    50% {
        transform: translate(-10rem, 0rem);
    }
    100% {
        transform: translate(0rem, 0rem);
    }
}

@media only screen and (min-width: 768px) and (min-aspect-ratio: 2/1) {
    .slide {
        &_0 {
            padding: 8rem 15.44rem;
            .container {
                max-height: 59rem;
            }
            .h1 {
                margin-top: 0;
            }
        }
    }
    .table {
        margin-top: 4.7rem;
        &__row {
            height: 6rem;
        }
    }
    .description {
        line-height: 3.73rem;
    }
}

.link {
    display: flex;
    padding: 0 3.4rem 0 2.8rem;
    text-decoration: none;
    // display: flex;
    align-items: center;
    justify-content: center;
    // min-width: 29.09rem;
    height: 5.31rem;
    font-size: 2.15rem;
    line-height: 2.69rem;
    color: var(--white);
    background: var(--bright-green);
    border-radius: 13.3rem;
    svg {
        margin-right: 1.4rem;
        width: 3.19rem;
        path {
            stroke: white;
        }
    }
    span {
        display: flex;
        align-items: center;
    }
}

@media only screen and (max-width: 767px) {
    .slide {
        padding: 4.75rem 0.75rem 2.75rem;
        justify-content: flex-start;
        .desc {
            margin-top: 0;
            font-size: 0.56rem;
            line-height: 1rem;
        }
        .text {
            margin-left: 0;
            margin-top: 0;
            width: 100%;
        }
        .h1, .h2, .h3, .h4 {
            margin-left: 0;
            margin-top: 0;
            margin-bottom: 0.75rem;
            width: 100%;
        }
        .h2 {
            font-size: 1.50rem;
            line-height: 1.63rem;
        }
        .h4 {
            margin-top: 0.38rem;
            margin-bottom: 0.35rem;
        }
        &_0 {
            padding: 4.75rem 0.75rem 3rem;
            .h1 {
                width: 16rem;
            }
            .container {
                padding-left: 0;
                max-height: unset;
            }
            .btn {
                position: static;
                margin: auto auto 0;
            }
            .text {
                width: 15rem;
            }
            .text+.text {
                margin-top: 1.4rem;
                width: 18rem;
            }
        }
        &_4 {
            .text+.text {
                width: 100%;
            }
        }
        &_5 {
            .container {
                padding-top: 0;
                padding-bottom: 1rem;
                justify-content: center;
                height: 100%;
            }
        }
        &_6 {
            .container {
                padding-top: 2.7rem;
            }
            .container {
                margin: auto;
                max-height: 24.3rem;
                height: 100%;
            }
        }
        &_7 {
            .link {
                margin-top: 0.8rem;
                margin-left: 0;
            }
            .container {
                padding-top: 1.8rem;
            }
            .container {
                margin: auto;
                max-height: 24.3rem;
                height: 100%;
            }
            .h2 {
                width: 14.6rem;
            }
        }
    }
    .link {
        margin-left: 0;
        margin-right: auto;
        display: flex;
        align-items: center;
        padding: 0;
        text-decoration: none;
        font-weight: bold;
        color: var(--bright-green);
        font-size: 1rem;
        line-height: 1.25rem;
        background: none;
        height: auto;
        span {
            display: flex;
            align-items: center;
        }
        svg {
            path {
                stroke: var(--bright-green) !important;
            }
            margin-right: 0.44rem;
            margin-top: 0.2rem;
            width: 1.44rem;
            height: 1.44rem;
        }
    }
    .table {
        margin-top: 0;
        &__row {
            margin-bottom: 0.7rem;
            padding: 0.56rem 0;
            font-size: 1.13rem;
            line-height: 1.63rem;
            flex-wrap: wrap;
            height: 5.44rem;
            align-items: flex-start;
            filter: none;
            &:nth-child(1) {
                display: none;
            }
            &.shown {
                opacity: 1;
                filter: none;
            }
        }
        &__col {
            display: flex;
            flex-direction: column;
            width: 33%;
            &:nth-child(1) {
                width: 100%;
            }
        }
        .hidden {
            display: flex;
            height: 1.13rem;
            font-size: 0.81rem;
            line-height: 1.06rem;
            font-weight: bold;
            font-family: 'Manrope';
            color: var(--dark-grey);
        }
        .star {
            font-size: 0.81rem;
            line-height: 1.06rem;
        }
    }
    .h4 {
        font-size: 1.50rem;
        line-height: 1.63rem;
    }
    .card {
        margin-top: auto;
        padding: 0.81rem 0.75rem;
        width: 18.50rem;
        margin-left: 0;
        border-radius: 0.50rem;
        .text {
            font-size: 0.81rem;
            line-height: 1.25rem;
            &+.text {
                margin-top: 1.25rem;
            }
        }
    }
    .container {
        display: flex;
        flex-direction: column;
    }
    .col {
        width: 100%;
        height: auto;
    }
    .text {
        font-size: 0.81rem;
        line-height: 1.25rem;
    }
    .btn {
        margin: auto auto 0;
        width: 4.00rem;
        height: 4.00rem;
        img {
            margin-right: 0.13rem;
            width: 1.88rem;
            height: 2.25rem;
        }
    }
    .description {
        margin-top: 1.25rem;
        margin-left: 0.75rem;
        font-size: 0.81rem;
        line-height: 1.25rem;
        width: 100%;
        span::after {
            left: -0.75rem;
            top: 0;
            width: 0.38rem;
            line-height: 1.25rem;
        }
    }
    .h1, .h2, .h3, .h4, .desc, .text, .ul, .description, .link {
        span, li {
            filter: none;
        }
    }
    .h1.shown, .h2.shown, .h3.shown, .h4.shown, .desc.shown, .text.shown, .ul.shown, .description.shown, .link.shown {
        span, li {
            filter: none;
        }
    }
    @keyframes btn {
        0% {
            transform: translate(0rem, 0rem);
        }
        50% {
            transform: translate(0rem, -1.5rem);
        }
        100% {
            transform: translate(0rem, 0rem);
        }
    }
}