:root {
    --white: #FFFFFF;
    --black: #000000;
    --dark-grey: #ABABAB;
    --light-grey: #F9F9F9;
    --emerald-green: #006525;
    --darkest-green: #14291C;
    --bright-green: #67D672;
    --red: #DE5D5D;
}

html {
    font-size: 0.625vw;
    width: 100%;
    height: 100%;
    background: var(--light-grey);
    -webkit-font-smoothing: antialiased;
}

body {
    position: relative;
    width: 100%;
    font-family: 'Manrope';
    background: var(--light-grey);
    color: var(--darkest-green);
    transition: opacity ease 0.5s;
    -webkit-font-smoothing: antialiased;
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    flex-shrink: 0;
}

sub {
    line-height: 0;
    font-size: 0.7em;
}

#root {
    position: relative;
    width: 100%;
    z-index: 10;
}

span {
    user-select: none;
}

.wrap {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.container {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 15.44rem;
}

img, svg {
    height: auto;
    width: 100%;
    flex-shrink: 0;
}

span, p, input, button, a, ul {
    font: inherit;
    color: inherit;
}

h1, h2, h3, h4 {
    font-family: 'Halvar Breit Rg';
    color: var(--emerald-green);
    font-weight: 700;
}

h1 {
    font-size: 7.5rem;
    line-height: 7.5rem;
}

h2 {
    font-size: 6.25rem;
    line-height: 6.25rem;
}

h3 {
    font-size: 5rem;
    line-height: 5rem;
}

h4 {
    font-size: 3.75rem;
    line-height: 3.75rem;
}

@media only screen and (max-width: 767px) {
    html {
        font-size: 5vw;
    }
    body, #root, .wrap{
        height: 100%;
        overflow: hidden;
    }
    body{
        overflow: hidden;
    }
    * {
        cursor: unset;
    }
    h1 {
        font-size: 2.00rem;
        line-height: 2.00rem;
    }
    h2 {
        font-size: 1.50rem;
        line-height: 1.63rem;
    }
    h3 {
        font-size: 1.25rem;
        line-height: 1.38rem;
    }
    h4 {
        font-size: 1.00rem;
        line-height: 1.25rem;
    }
}