.wrap {
    padding: 0 2.94rem;
    position: fixed;
    bottom: 2.50rem;
    left: 50%;
    transform: translate(-50%);
    height: 3.88rem;
    width: 104.19rem;
    backdrop-filter: blur(12.5px);
    background: rgba(255, 255, 255, 0.75);
    border-radius: 6.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
	z-index: 110;
}
.section{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 4rem;
    height: 3.00rem;
    z-index: 1;
    cursor: pointer;
    user-select: none;
    &:last-child{
        margin-right: 0;
    }
    &::before{
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        height: 100%;
        width: calc(100% + 5rem);
        z-index: -1;
        border-radius: 6.25rem;
        background: var(--emerald-green);
        opacity: 0;
        transition: opacity ease-in-out 0.15s;
    }
    &__title{
        font-size: 1.50rem;
        line-height: 2rem;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.3em;
        font-family: 'Manrope';
        color: var(--bright-green);
        opacity: 1;
        transition: all ease-in-out 0.15s;
    }
    &__dots{
        position: absolute;
        display: flex;
        opacity: 0;
        transition: all ease-in-out 0.15s;
    }
    &.active{
        cursor: unset;
        pointer-events: none;
        &::before{
            opacity: 1;
        }
        .section__dots{
            opacity: 1;
            .dot{

                pointer-events: auto;
            }
        }
        .section__title{
            color: var(--emerald-green);
            opacity: 0;
            pointer-events: none;
        }
    }
    &:nth-child(1){
        &.active{
            .section__title{
                color: var(--white);
                opacity: 1;
                pointer-events: auto;
            }
        }
    }
    &:nth-child(2){
       &::before{
        width: calc(100% + 4.3rem);
       }
    }
}
.dot {
    position: relative;
    margin-right: 3.38rem;
    width: 1.25rem;
    height: 1.25rem;
    border-radius: 50%;
    cursor: pointer;
    
    pointer-events: none;
    &::after {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
		border-radius: 50%;
        transform: translate(-50%, -50%) scale(0.6);
        
        background: var(--white);
        opacity: 0.5;
        transition: opacity ease 0.3s, transform ease 0.5s;
    }
    &:last-child{
        margin-right: 0;
    }
    &.active {
        &::after {
			opacity: 1;
            transform: translate(-50%, -50%) scale(1);
        }
    }
}
@media only screen and (max-width: 767px) {
    .wrap {
        bottom: 0.75rem;
        width: 5.00rem;
        height: 1.50rem;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Halvar Breit Rg';
        color: var(--emerald-green);
    }
}