.wrap {
    position: fixed;
    left: 0;
    top: 0;
    padding: 2.50rem 2.50rem 0;
    width: 100%;
    z-index: 1000;
    display: flex;
    align-items: center;
}

.menu {
    opacity: 0;
    pointer-events: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(0.75rem);
    z-index: 11;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__inner {
        padding: 15.13rem 15.44rem 9.81rem;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}

.list {
    display: grid;
    gap: 2.50rem;
    height: 100%;
    max-height: 58.44rem;
    grid-template-columns: 76.25rem 50.38rem;
    // grid-template-rows: 56% 39%;
    grid-template-rows: auto 22.75rem;
}
.lang {
    margin-left: auto;
    display: flex;
    &__link {
        margin-left: 1.2rem;
        text-decoration: none;
        font-size: 1.50rem;
        line-height: 2.25rem;
        font-family: 'Halvar Breit Rg';
        font-weight: 700;
        text-transform: uppercase;
        color: var(--emerald-green);
        &_disable {
            pointer-events: none;
            color: var(--dark-grey);
        }
    }
}

.item {
    position: relative;
    padding: 2.44rem 3.38rem;
    height: 100%;
    max-height: 58.44rem;
    background: var(--light-grey);
    overflow: hidden;
    &:nth-child(1) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 3;
    }
    &:nth-child(2) {
        // grid-column-start: 2;
        // grid-column-end: 3;
        // grid-row-start: 1;
        // grid-row-end: 3;
        .item__title {
            font-size: 3.75rem;
            line-height: 3.75rem;
        }
        .item__bg img {
            height: 100%;
            object-fit: cover;
        }
    }
    &:nth-child(3) {
        .item__title {
            font-size: 3.75rem;
            line-height: 3.75rem;
        }
        .item__bg {
            display: none;
        }
    }
    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        top: 0;
        right: 0;
        z-index: 0;
        picture{
            display: flex;
            width: 100%;
        }
        img {
            margin-top: auto;
        }
        // canvas{
        //     width: 100% !important;
        //     height: auto !important;
        // }
    }
    &__list {
        margin-top: 2.75rem;
        position: relative;
        z-index: 10;
        display: flex;
        flex-direction: column;
    }
    &__title {
        position: relative;
        font-family: 'Halvar Breit Rg';
        font-size: 5.00rem;
        line-height: 5.00rem;
        font-weight: 700;
        color: var(--emerald-green);
        z-index: 10;
        text-decoration: none;
    }
    &__link {
        margin-bottom: 0.5rem;
        font-size: 2.25rem;
        line-height: 3.63rem;
        color: var(--emerald-green);
        font-weight: 500;
        text-decoration: none;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__qr {
        position: absolute;
        top: 6.5rem;
        right: 3.2rem;
        width: 13.25rem;
        height: 13.25rem;
        z-index: 10;
    }
}

.footer {
    margin-top: 6.69rem;
    display: flex;
    align-items: center;
    width: 100%;
    .lang {
        &__link {
            margin-left: 2.9rem;
            font-size: 3.75rem;
            line-height: 3.75rem;
            color: var(--bright-green);
            &_disable {
                color: var(--dark-grey);
            }
        }
    }
}

.infografika {
    margin-right: auto;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    span {
        font-size: 1.13rem;
        line-height: 1.5rem;
        font-weight: 500;
        color: var(--emerald-green);
    }
    img {
        margin-top: 0.69rem;
        width: 17.63rem;
    }
}

.burger {
    position: relative;
    margin-right: 7.63rem;
    width: 5.31rem;
    height: 5.31rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 0.13rem solid var(--emerald-green);
    cursor: pointer;
    z-index: 12;
    span {
        position: absolute;
        left: 50%;
        top: 50%;
        // margin: 0.31rem 0;
        display: block;
        height: 0.13rem;
        width: 2.44rem;
        background: var(--emerald-green);
        &:nth-child(1) {
            transform: translate(-50%, calc(-50% - 0.62rem));
        }
        &:nth-child(2) {
            transform: translate(-50%, calc(-50%));
        }
        &:nth-child(3) {
            transform: translate(-50%, calc(-50% + 0.62rem));
        }
    }
    &.active {
        span {
            &:nth-child(1) {
                transform: translate(-50%, -50%) rotate(45deg);
            }
            &:nth-child(2) {
                opacity: 0;
            }
            &:nth-child(3) {
                transform: translate(-50%, -50%) rotate(-45deg);
            }
        }
    }
}

.logo {
    position: relative;
    margin-right: 12.94rem;
    width: 12.88rem;
    z-index: 13;
}

.nav {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 1.5rem;
    line-height: 2.25rem;
    color: var(--emerald-green);
    font-family: 'Halvar Breit Rg';
    z-index: 0;
}

.link {
    position: relative;
    display: flex;
    margin-right: 5.00rem;
    text-decoration: none;
    z-index: 10;
    &::before {
        position: absolute;
        content: '';
        left: 50%;
        top: 1.44rem;
        transform: translate(-50%);
        width: calc(100% + 0.5rem);
        height: 0.44rem;
        background: var(--bright-green);
        z-index: -1;
        opacity: 0;
    }
    &.active {
        &::before {
            opacity: 1;
        }
    }
}

@media only screen and (max-width: 767px) {
    .wrap {
        padding: 0.75rem 0.75rem 0;
    }
    .menu {
        height: 100%;
        overflow: hidden scroll;
        &__inner{
            height: auto;
            padding: 4.81rem 0.75rem 1.69rem;
            align-items: flex-start;
            justify-content: center;
        }
    }
    .list {
        // display: flex;
        // flex-direction: column;6.
        grid-template-columns: 100%;
        grid-template-rows: auto;
        min-height: unset;
        height: auto;
        width: 100%;
        gap: 0.75rem;
    }
    .item{
        height: auto;
        padding: 1rem 0.75rem;
        &__title{
            font-size: 1.38rem;
            line-height: 2rem;
        }
        &__list{
            margin-top: 0.75rem;
        }
        &__link{
            margin-bottom: 0.75rem;
            font-size: 1.00rem;
            line-height: 1.25rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
        &__bg{
            display: none;
        }
        &__qr{
            display: none;
        }
        &:nth-child(2), &:nth-child(3) {
            .item__title {
                font-size: 1.38rem;
                line-height: 2rem;
            }
        }
    }
    .footer{
        margin-top: 1.25rem;
        .lang{
            display: none;
        }
    }
    .infografika {
        span {
            font-size: 0.81rem;
            line-height: 1.25rem;
        }
        img {
            margin-top: 0.31rem;
            width: 9.56rem;
        }
    }
    .burger {
        margin-right: 1.06rem;
        width: 2.00rem;
        height: 2.00rem;
        border: 1px solid var(--emerald-green);
        span {
            height: 1px;
            width: 1.00rem;
            &:nth-child(1) {
                transform: translate(-50%, calc(-50% - 0.31rem));
            }
            &:nth-child(2) {
                transform: translate(-50%, calc(-50%));
            }
            &:nth-child(3) {
                transform: translate(-50%, calc(-50% + 0.31rem));
            }
        }
    }
    .logo {
        margin-right: 0;
        width: 4.75rem;
    }
    .nav {
        display: none;
    }
    .lang {
        &__link {
            margin-left: 0.88rem;
            font-size: 1rem;
            line-height: 1.5rem;
        }
    }
}