    flex-direction: column;
    justify-content: center;
    user-select: none;
    &_mobile {
        width: 100%;
    }
    &.active {
        opacity: 1;
        z-index: 100;
        .clock {
            .clock__second {
                animation: second 60s steps(60, end) 2s infinite;
            }
            .clock__minute {
                animation: hour 1.5s ease-in-out 0.5s 1;
            }
            .clock__hour {
