    width: 3.50rem;
    height: 3.50rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.clock {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 62.38rem;
    height: 62.38rem;
    transition: all ease-in-out 0.5s;
    opacity: 0;
    &.shown {
        opacity: 1;
    }
    &__bg {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 94.5%;
        height: 94.5%;
        transform: translate(-50%, -50%);
    }
    &__second {
        position: absolute;
        bottom: 50%;
        left: 50%;
        transform: translate(-50%, 0%);
        transform-origin: bottom;
        // height: 29.50rem;
        height: 47.5%;
        width: 0.31rem;
        background: var(--darkest-green);
    }
    &__minute {
        position: absolute;
        bottom: 50%;
        left: 50%;
        // height: 25.56rem;
        height: 41%;
        width: 1.56rem;
        transform: translate(-50%, 0%);
        transform-origin: bottom;
        background: var(--darkest-green);
    }
    &__hour {
        position: absolute;
        bottom: 50%;
        left: 50%;
        // height: 16.06rem;
        height: 26%;
        width: 1.56rem;
        transform: translate(-50%, 0%);
        transform-origin: bottom;
        background: var(--darkest-green);
    }
    &__circle {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 4rem;
        height: 4.00rem;
        border-radius: 50%;
        background: var(--darkest-green);
    }
}

.container {
    position: relative;
    display: flex;
    height: 100%;
    width: 100%;
    max-height: 55rem;
}

.desc {
    font-size: 1.5rem;
    line-height: 2rem;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 0.32em;
    color: var(--darkest-green);
}

.btn {
    margin-top: auto;
    margin-left: 10rem;
    width: 8.75rem;
    height: 8.75rem;
    border-radius: 62.50rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bright-green);
    animation: btn 3s ease-in-out infinite;
    transition: all ease-in-out 0.5s;
    opacity: 0;
    &.shown {
        opacity: 1;
    }
    img {
        margin-right: 0.28rem;
        width: 4.06rem;
        height: 4.94rem;
    }
    &__text {
        width: 0;
        transition: width ease-in-out 0.5s;
        overflow: hidden;
    }
    &__inner {
        padding-left: 2.63rem;
        white-space: nowrap;
        font-size: 1.50rem;
        color: var(--white);
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.32em;
    }
    // &:hover {
    //     width: 51.63rem;
    //     .btn__text {
    //         width: 39.59rem;
    //     }
    // }
}

.play {
    position: absolute;
    width: 15.63rem;
    height: 15.63rem;
    left: 50%;
    top: 50%;
    transform: translate(-50%, calc(-50% + 20vh));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bright-green);
    transition: all ease-in-out 0.5s;
    opacity: 0;
    pointer-events: none;
    z-index: 110;
    cursor: pointer;
    &.shown {
        opacity: 1;
        pointer-events: auto;
    }
    svg {
        margin-left: 0.7rem;
        width: 5.63rem;
    }
}

.h1 {
    font-family: 'Halvar Breit Rg';
    color: var(--emerald-green);
    font-weight: 700;
    font-size: 7.5rem;
    line-height: 7.5rem;
}
@media only screen and (max-width: 767px) {
    .h1 {
        font-size: 2.00rem;
        line-height: 2.00rem;
    }
}

.h1, .h2, .h3, .h4, .desc, .text, .ul, .link {
    transform: translateZ(0);
    span, li {
        opacity: 0;
        filter: blur(0.25rem);
        transition-property: opacity, filter;
        // will-change: opacity, filter;
        transition-duration: 0.4s, 0.4s;
        transform: translateZ(0);
    }
}

.h1.shown, .h2.shown, .h3.shown, .h4.shown, .desc.shown, .text.shown, .ul.shown, .link.shown {
    span, li {
        opacity: 1;
        filter: blur(0);
        &:nth-child(1) {
            transition-delay: 0s, 0s;
        }
        &:nth-child(2) {
            transition-delay: 0.05s, 0.05s;
        }
        &:nth-child(3) {
            transition-delay: 0.1s, 0.1s;
        }
        &:nth-child(4) {
            transition-delay: 0.15s, 0.15s;
        }
        &:nth-child(5) {
            transition-delay: 0.2s, 0.2s;
        }
        &:nth-child(6) {
            transition-delay: 0.25s, 0.25s;
        }
        &:nth-child(7) {
            transition-delay: 0.3s, 0.3s;
        }
        &:nth-child(8) {
            transition-delay: 0.35s, 0.35s;
        }
        &:nth-child(9) {
            transition-delay: 0.4s, 0.4s;
        }
        &:nth-child(10) {
            transition-delay: 0.45s, 0.45s;
        }
        &:nth-child(11) {
            transition-delay: 0.5s, 0.5s;
        }
        &:nth-child(12) {
            transition-delay: 0.55s, 0.55s;
        }
        &:nth-child(13) {
            transition-delay: 0.6s, 0.6s;
        }
        &:nth-child(14) {
            transition-delay: 0.65s, 0.65s;
        }
        &:nth-child(15) {
            transition-delay: 0.7s, 0.7s;
        }
        &:nth-child(16) {
            transition-delay: 0.75s, 0.75s;
        }
        &:nth-child(17) {
            transition-delay: 0.8s, 0.8s;
        }
    }
}

.text {
    font-size: 2.50rem;
    line-height: 3.63rem;
    font-weight: 500;
}

.qr {
    display: flex;
    transition: all ease-in-out 0.5s;
    opacity: 0;
    &.shown {
        opacity: 1;
    }
    img {
        margin-right: 3.1rem;
        width: 15.00rem;
        height: 15.00rem;
    }
    .text {
        width: 62rem;
    }
}

@keyframes second {
    0% {
        transform: translate(-50%, 0%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, 0%) rotate(360deg);
    }
}

@keyframes minute {
    0% {
        transform: translate(-50%, 0%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, 0%) rotate(4320deg);
    }
}

@keyframes hour {
    0% {
        transform: translate(-50%, 0%) rotate(0deg);
    }
    100% {
        transform: translate(-50%, 0%) rotate(360deg);
    }
}

@keyframes btn {
    0% {
        transform: translate(0rem, 0rem);
    }
    50% {
        transform: translate(-10rem, 0rem);
    }
    100% {
        transform: translate(0rem, 0rem);
    }
}

@media only screen and (min-width: 768px) and (min-aspect-ratio: 2/1) {
    .clock {
        width: 54rem;
        height: 54rem;
    }
}

