.wrap {
    opacity: 0;
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: opacity ease 0.3s;
    &.hide {
        opacity: 0 !important;
    }
}

.overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 200vw;
    width: 200vw;
    background: var(--light-grey);
    border-radius: 50%;
    z-index: 10;
}

.body, .points, .svgs, .svg, .container, .labels, .charts, .countries {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.charts {
    z-index: 9;
}

.chart {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 53.13rem;
    height: 53.13rem;
    &:nth-child(1) {
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-90deg);
        }
        span {
            position: absolute;
            left: 50%;
            top: 30.56rem;
            transform: translate(-50%);
            font-size: 10.00rem;
            line-height: 10.00rem;
            font-weight: 700;
            color: var(--white);
            font-family: 'Halvar Breit Rg';
            z-index: 1;
        }
    }
    &:nth-child(2) {
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
    &__logo {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 2;
        span {
            margin-top: 2.31rem;
            font-size: 2.50rem;
            line-height: 2.50rem;
            font-weight: 700;
            color: var(--white);
            font-family: 'Halvar Breit Rg';
            z-index: 1;
            text-align: center;
        }
        &:nth-child(1) {
            left: 4rem;
            top: 16.8rem;
            img {
                width: 20.06rem;
            }
        }
        &:nth-child(2) {
            left: 33.9rem;
            top: 16.8rem;
            img {
                width: 10.94rem;
            }
            span {
                margin-top: 1.69rem;
            }
        }
        &:nth-child(3) {
            left: 20.2rem;
            top: 38rem;
            img {
                width: 12.69rem;
            }
        }
    }
}

.mainPoints .point {
    &:nth-child(3) {
        fill: var(--bright-green);
    }
    &:nth-child(2) {
        stroke: var(--bright-green);
        stroke-width: 5;
        animation: pulse 3s linear infinite;
    }
    &:nth-child(1) {
        stroke: var(--bright-green);
        stroke-width: 5;
        animation: pulse 3s linear 1.5s infinite;
    }
}

.cityPoints .point {
    opacity: 0;
    &_0 {
        fill: var(--bright-green);
    }
    &_1 {
        fill: var(--emerald-green);
    }
    &_2 {
        fill: var(--bright-green);
    }
    &_3 {
        fill: var(--emerald-green);
    }
    &_4 {
        fill: var(--bright-green);
    }
    &_5 {
        fill: var(--bright-green);
    }
    &_6 {
        fill: var(--bright-green);
    }
}

.container {
    top: 50%;
    left: 50%;
    height: auto;
}

.body {
    position: relative;
    width: 314%;
    height: auto;
}

.bg {
    position: relative;
    width: 100%;
    height: 100%;
    &__tiles {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        opacity: 0.5;
    }
    &_tile {
        display: flex;
        width: 20%;
        height: auto;
        margin: -0.1rem 0;
        transform: translateZ(0);
        picture {
            display: flex;
            width: 100%;
        }
    }
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--light-grey);
        z-index: 10;
    }
}

.svgs {
    z-index: 11;
}

.svg {
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    display: flex;
    height: auto;
    width: 100%;
    z-index: 100;
    &_1 {
        width: 100%;
        z-index: 101;
    }
    &_2 {
        width: 116.13rem;
        left: 50%;
        transform: translate(45.9%, -0.8%);
    }
    path {
        will-change: opacity, stroke-dashoffset;
    }
}

.labels {
    z-index: 102;
}

.countries {
    z-index: 102;
}

.label {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1.5rem;
    height: 3.00rem;
    font-size: 1.38rem;
    line-height: 2.5rem;
    letter-spacing: 0.2em;
    font-weight: 700;
    text-transform: uppercase;
    color: var(--white);
    background: var(--darkest-green);
    border-radius: 6.25rem;
    z-index: 10;
    opacity: 0;
    &_0 {
        top: 76.8%;
        left: 83.8%;
    }
    &_1 {
        top: 48.6%;
        left: 16%;
    }
    &_2 {
        top: 54.6%;
        left: 67%;
    }
    &_3 {
        top: 55.4%;
        left: 61.4%;
    }
    &_4 {
        top: 40.5%;
        left: 50.6%;
    }
    &_5 {
        top: 41.5%;
        left: 30%;
    }
    &_6 {
        top: 45%;
        left: 25.9%;
    }
    &_7 {
        top: 50.6%;
        left: 38%;
    }
}

.country {
    position: absolute;
    display: flex;
    font-size: 1.75rem;
    letter-spacing: 0.2em;
    font-weight: 700;
    text-transform: uppercase;
    z-index: 10;
    color: #dadada;
}

@keyframes pulse {
    0% {
        transform: scale(0);
        stroke-width: 4;
        opacity: 1;
    }
    40% {
        transform: scale(2);
        opacity: 0.9;
        stroke-width: 1.5;
    }
    100% {
        transform: scale(4);
        opacity: 0;
        stroke-width: 0;
    }
}

@media only screen and (min-width: 768px) and (min-aspect-ratio: 2/1) {
    .label {
        &_0 {
            top: 68.8%;
            left: 88.8%;
        }
    }
}

@media only screen and (max-width: 767px) {
    .body {
        width: 1250%;
        will-change: transform;
    }
    .overlay {
        height: 150vh;
        width: 150vh;
        // will-change: transform;
    }
    .country {
        font-size: 1rem;
    }
    .label {
        padding: 0 0.75rem;
        height: 1.06rem;
        font-size: 0.56rem;
        line-height: 0.56rem;
        &_0 {
            top: 74.2%;
            left: 83.7%;
        }
        &_2 {
            top: 54.2%;
            left: 66.9%;
        }
        &_3 {
            top: 55.8%;
            left: 63.4%;
        }
        &_5 {
            top: 40.5%;
            left: 30.3%;
        }
        &_6 {
            top: 44.3%;
            left: 26%;
        }
    }
    .cityPoints .point {
        transform: translateZ(0);
        &_1 {
            cx: 2142;
            cy: 1132;
            r: 4;
        }
        &_2 {
            r: 4;
        }
        &_3 {
            cx: 1712;
            cy: 773;
            r: 4;
        }
        &_4 {
            cx: 710.5;
            cy: 603.2;
        }
    }
    .charts {
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    .chart {
        width: 11.25rem;
        height: 11.25rem;
        transform: translate(-50%, 21%);
        &:nth-child(1) {
            span {
                top: 6.6rem;
                font-size: 2.00rem;
                line-height: 2.5rem;
            }
        }
        &__logo {
            span {
                margin-top: 0.51rem;
                font-size: 1rem;
                line-height: 1rem;
            }
            &:nth-child(1) {
                left: 0.9rem;
                top: 3.6rem;
                img {
                    width: 4.25rem;
                }
            }
            &:nth-child(2) {
                left: 6.5rem;
                top: 3.5rem;
                img {
                    width: 2.4rem;
                }
                span {
                    margin-top: 0.4rem;
                }
            }
            &:nth-child(3) {
                left: 3.7rem;
                top: 8rem;
                img {
                    width: 2.7rem;
                }
            }
        }
    }
    .svg {
        &_2 {
            width: 57.88rem;
        }
    }
    .bg {
        &_tile {
            opacity: 0;
        }
    }
    .mainPoints {
        .point {
            &:nth-child(3) {
                r: 10;
            }
        }
    }
}