.wrap {
    position: relative;
    width: 100%;
    height: 100%;
}
.particles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    pointer-events: none;
}
.map{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    pointer-events: none;
}
.slides{
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 101;
}
