.wrap {
    position: relative;
    width: 100%;
    height: 100%;
    canvas {
        width: 100% !important;
        height: 100% !important;
        object-fit: cover;
        transform: translateZ(0);
    }
}

.slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--light-grey);
    display: flex;
    min-height: 33.75rem;
    opacity: 0;
    transition: opacity ease 0.5s;
    img {
        margin-top: auto;
    }
}

@media only screen and (max-width: 767px) {
    .wrap {
        display: flex;
        canvas {
			margin-top: auto;
            height: auto!important;
        }
    }
}