.wrap {
    padding: 0 3.25rem;
    position: fixed;
    bottom: 2.50rem;
    left: 50%;
    transform: translate(-50%);
    height: 3.88rem;
    backdrop-filter: blur(12.5px);
    background: rgba(255, 255, 255, 0.75);
    border-radius: 6.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 110;
}

.dot {
    margin-right: 2.63rem;
    position: relative;
    width: 1.25rem;
    height: 1.25rem;
    border-radius: 50%;
    cursor: pointer;
    &::after {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        transform: translate(-50%, -50%) scale(0.6);
        background: var(--dark-grey);
        transition: background ease 0.3s, transform ease 0.5s;
    }
    &.active {
        &::after {
            background: var(--emerald-green);
            transform: translate(-50%, -50%) scale(1);
        }
    }
    &:last-child {
        margin-right: 0;
    }
}

@media only screen and (max-width: 767px) {
    .wrap {
        bottom: 0.75rem;
        width: 5.00rem;
        height: 1.50rem;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Halvar Breit Rg';
        color: var(--emerald-green);
    }
}