.wrap {
    position: relative;
    width: 100%;
    height: 100%;
}
.particles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    pointer-events: none;
}
.map{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    pointer-events: none;
}
.slides{
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 101;
    // pointer-events: none;
}
.popup{
    position: fixed;
    z-index: 100000;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: var(--light-grey);
    transform: translateZ(100px);
    opacity: 0;
    pointer-events: none;
    transition: opacity ease 0.3s;
    &.open{
        opacity: 1;
        pointer-events: auto;
    }
    &__close{
        position: absolute;
        top: 3.13rem;
        right: 3.13rem;
        width: 7.50rem;
        height: 7.50rem;
        cursor: pointer;
    }
    &__video{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) translateZ(100px);
        width: 111.94rem;
        height: 57.88rem;
        z-index: 1002;
        video{
            position: relative;
            width: 100%;
            height: auto;
            z-index: 1002;
            transform: translateZ(100px);
        }
    }
}
@media only screen and (max-width: 767px) {
    .popup{
        &__close{
            top: 0.75rem;
            right: 0.75rem;
            width: 3.06rem;
            height: 3.06rem;
        }
        &__video{
        width: 18.5rem;
        height: 9.56rem;
    }
    }   
}