.wrap {
    opacity: 0;
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: opacity ease 0.3s;
    &.hide {
        opacity: 0 !important;
    }
    img {
        user-select: none;
    }
}

.overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 200vw;
    width: 200vw;
    transform: translate(-50%, -50%) scale(1);
    background: var(--light-grey);
    border-radius: 50%;
    z-index: 10;
}

.body, .vehicles, .clouds, .container, .gradient, .stats {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.gradient {
    background: linear-gradient(269.84deg, #F9F9F9 62.46%, rgba(249, 249, 249, 0) 99.86%);
    z-index: 1;
}

.container {
    top: 50%;
    left: 50%;
    height: auto;
}

.body {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: auto;
}

.vehicles {
    z-index: 9;
    overflow: hidden;
}

.stats {
    padding: 8rem 0;
    z-index: 30;
    right: 2.50rem;
    left: unset;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 7.88rem;
    height: 100vh;
    max-height: 90.00rem;
    &__group {
        margin: 1rem 0;
        padding: 1.13rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 22.94rem;
        border-radius: 6.25rem;
        backdrop-filter: blur(0.75rem);
        background: rgba(255, 255, 255, 0.75);
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.stat {
    position: relative;
    width: 5.63rem;
    height: 5.63rem;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
        position: absolute;
        transform: rotate(-90deg);
        width: 100%;
    }
    img {
        width: 2.06rem;
    }
    span {
        display: flex;
        font-size: 1.69rem;
        line-height: 2.31rem;
        font-weight: 700;
        font-family: 'Manrope';
        sup {
            margin-left: 0.1em;
            font-size: 0.65em;
            line-height: 1.8em;
        }
    }
    &:nth-child(2) {
        span {
            margin-bottom: 0.1rem;
            font-size: 2.31rem;
            line-height: 2.31rem;
            font-weight: 600;
        }
    }
}

.grounds {
    z-index: 20;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    overflow: hidden;
    pointer-events: none;
}

.ground {
    position: relative;
    width: 160.13rem;
    height: auto;
    animation: grounds 20s linear infinite;
    picture{
        display: flex;
    }
}

.clouds {
    z-index: 20;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    overflow: hidden;
    pointer-events: none;
}

.cloud {
    position: relative;
    width: 190.06rem;
    height: auto;
    animation: clouds 10s linear infinite;
}

.airplane {
    position: absolute;
    left: 14rem;
    top: 12rem;
    width: 17.69rem;
}

.train {
    position: absolute;
    left: -49.2rem;
    top: 42.4rem;
    width: 81.56rem;
}

.ship {
    position: absolute;
    left: 4rem;
    top: 72.3rem;
    width: 28.31rem;
}

.bg {
    position: relative;
    width: 100%;
    height: 100%;
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--light-grey);
        z-index: 10;
    }
    &::after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 4rem;
        background: linear-gradient(to bottom, var(--light-grey), transparent);
        opacity: 0;
        transition: opacity ease 0.3s;
        z-index: 10;
    }
    &::before {
        position: absolute;
        content: '';
        bottom: 0;
        left: 0;
        width: 100%;
        height: 4rem;
        background: linear-gradient(to top, var(--light-grey), transparent);
        opacity: 0;
        transition: opacity ease 0.3s;
        z-index: 10;
    }
}

@keyframes clouds {
    0% {
        transform: translate(0%)
    }
    100% {
        transform: translate(-100%)
    }
}

@keyframes grounds {
    0% {
        transform: translate(0%)
    }
    100% {
        transform: translate(-100%)
    }
}

@media only screen and (min-width: 768px) and (min-aspect-ratio: 2/1) {
    .stats {
        width: 8.75vh;
        &__group {
            margin: 1.11vh 0;
            padding: 1.25vh;
            height: 25.486vh;
            border-radius: 6.95vh;
            &:first-child {
                margin-top: 0;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .stat {
        width: 6.25vh;
        height: 6.25vh;
        img {
            width: 2.29vh;
        }
        span {
            font-size: 1.875vh;
            line-height: 2.569vh;
        }
        &:nth-child(2) {
            span {
                font-size: 2.569vh;
                line-height: 2.569vh;
            }
        }
    }
    .ground {
        width: 178vh;
    }
    .airplane {
        position: absolute;
        // left: 14rem;
        left: 15.4vh;
        top: 13vh;
        // width: 17.69rem;
        width: 19.65vh;
        //will-change: transform;
    }
    .train {
        position: absolute;
        // left: -49.2rem;
        left: -55vh;
        top: 47.3vh;
        // width: 81.56rem;
        width: 90.625vh;
        //will-change: transform;
    }
    .ship {
        position: absolute;
        // left: 4rem;
        left: 4.5vh;
        top: 80.7vh;
        // width: 28.31rem;
        width: 31.458vh;
        //will-change: transform;
    }
}

@media (max-aspect-ratio: 16/9) {
    .bg {
        &::after, &::before {
            opacity: 1;
        }
    }
}

@media only screen and (max-width: 767px) {
    .overlay {
        height: 150vh;
        width: 150vh;
    }
    .label {
        padding: 0 0.75rem;
        height: 1.06rem;
        font-size: 0.56rem;
        line-height: 0.56rem;
    }
    .container {
        height: 100%;
    }
    .body {
        height: 100%;
    }
    .gradient {
        background: linear-gradient(180deg, #F9F9F9 62.46%, rgba(249, 249, 249, 0) 99.86%);
        z-index: 100;
    }
    .stats {
        display: none;
    }
    .grounds, .clouds {
        // flex-direction:column-reverse;
        display: block;
        position: absolute;
        top: unset;
        bottom: 0;
        left: 0;
        height: auto;
    }
    .grounds {
        z-index: -1;
    }
    .ground {
        margin: -1px;
        width: 100%;
        picture {
            display: flex;
            width: 100%;
        }
    }
    .cloud {
        width: 100%;
        picture {
            display: flex;
        }
    }
    .airplane {
        left: 2.8rem;
        top: unset;
        bottom: 2.8rem;
        width: 3.94rem;
    }
    .train {
        left: 1.2rem;
        top: unset;
        bottom: -2.8rem;
        width: 18.13rem;
    }
    .ship {
        left: 13.8rem;
        top: unset;
        bottom: 3rem;
        width: 6.31rem;
    }
    .bg {
        overflow: hidden;
        &::after {
            height: 3.75rem;
            background: linear-gradient(180deg, #F9F9F9 62.46%, rgba(249, 249, 249, 0) 99.86%)
        }
        &::before {
            display: none;
        }
    }
    @keyframes clouds {
        0% {
            transform: translate(0%, 0%)
        }
        100% {
            transform: translate(0%, 100%)
        }
    }
    @keyframes grounds {
        0% {
            transform: translate(0%, 0%)
        }
        100% {
            transform: translate(0%, 100%)
        }
    }
}